'use server'

import { getUserPermissions } from "@/utils/permissions";
import { revalidatePath } from "next/cache";

export async function deleteDatabaseAction() {
  // 1. SECURITY CHECK: Fetch permissions on the server
  const permissions = await getUserPermissions();

  // 2. The Gatekeeper
  if (!permissions.includes('users.delete')) {
    return { success: false, message: "❌ SECURITY ALERT: Unauthorized attempt blocked!" };
  }

  // 3. The Action (Simulated)
  console.log("DATABASE DELETED BY ADMIN");
  return { success: true, message: "✅ Success: Database has been deleted." };
}